import React from "react";
import { usePortfolio } from "../../../context/PortfolioContext";
import Button from "../../ui/Button";
import { HashLink as Link } from "react-router-hash-link";

import "./Home.css";
import ProjectHighlight from "./ProjectHighlight";

export default function Home() {
  const { general, projectHighlights } = usePortfolio();
  return (
    <section className="portfolio-home" id="home">
      <div className="home-hero">
        <h1><span></span>{general.name}</h1>
        <h3>{general.description}</h3>
        <div className="home-cta">
          <Button variant="primary">See Resum√©</Button>
          <Button variant="outline-primary">Contact Me</Button>
        </div>
        {/* <p>
          Scroll down<i className="fas fa-chevron-down"></i>
        </p> */}
      </div>
      <div className="home-highlights">
        <div className="highlights-control">
          <h3>Project Highlights</h3>
          <Link to="#projects">
            See more <i className="fas fa-angle-right"></i>
          </Link>
        </div>
        <div className="highlights-carousel">
          {projectHighlights.map((project, index) => (
            <ProjectHighlight project={project} key={index} />
          ))}
        </div>
      </div>
    </section>
  );
}
