import React from "react";
import "./ProjectHighlight.css";
import Button from "../../ui/Button"
export default function ProjectHighlight({ project }) {
  return (
    <div className="project-highlight">
      <img src={project.imageUrl} alt="" />
      <span></span>
      <div className="highlight-info">
        <h3>{project.title}</h3>
        <Button variant="primary">Info</Button>
      </div>
      {/* <div className="project-highlight-info">
        <p>{project.description}</p>
        <div className="project-highlight-cta">
          <button className="project-highlight-live">Live Demo</button>
          <button className="project-highlight-code">See Code</button>
        </div>
      </div> */}
    </div>
  );
}
