import React from "react";
import Ripples from "react-ripples";

import "./Button.css";

// const variants = ["text", "primary", "secondary", "danger"]
export default function Button({
  children,
  type,
  onClick,
  className,
  variant,
  disabled = false,
  size,
  startIcon,
  endIcon,
}) {
  return (
    <Ripples during={800}>
      <button
        className={`button ${variant} ${
          disabled && "disabled"
        } ${size} ${className}`}
        type={type || "button"}
        onClick={disabled ? () => {} : onClick}
      >
        {startIcon && <i className={`start-icon ${startIcon}`}></i>}
        {children}
        {endIcon && <i className={`end-icon ${endIcon}`}></i>}
      </button>
    </Ripples>
  );
}
